import styled from 'styled-components';

export default styled.section `
  padding: 0em 4em;
  background: #f3f3f3;
  width: 100%;
  height: 600px;
  position: relative;
  margin: 0px auto 4em auto;
  @media only screen and (max-width: 600px) {
    margin: 0px auto;
    padding: 0em;
    height: 500px;
}
`;